import React, { Component } from 'react';
import RuleList from "../../vibe/components/rule-list";
import {PlusIcon,XIcon,ChevronDownIcon, ChevronUpIcon} from '@primer/octicons-react'
import NameForm from "../../vibe/components/AddRule";

export default class Rules extends Component {
    constructor(props){
        super(props);
        this.openAddRule = this.openAddRule.bind(this);
        this.state = {
            addRule: false
        };
    }

    openAddRule = () => {
        this.setState({addRule : true});

    };

    closeAddRule = () => {
        this.setState({addRule: false});
    };

    render(){
        const addRule = this.state.addRule;
        const boxStyle = {
            paddingTop: "10px",
        };
        const buttonStyle = {
            display : "inline-block",
        };

        let button;
        if(addRule){
            button = <button className="btn btn-dark" onClick={this.closeAddRule}> <XIcon size={24}/> Close </button>
        }
        else {
            button = <button className="btn btn-dark" onClick={this.openAddRule}> <PlusIcon size={24}/> Add Rule </button>
        }

        return(
            <div className="mainClass">
                <div className="buttonClass">
                    <div style={buttonStyle}>{button}</div>
                   <div>{ addRule
                       ? <div><br/><NameForm/></div>
                       : <div></div>
                   }
                    </div>
                </div>
                <div style={boxStyle}>
                <RuleList/>
                </div>
            </div>
        );
    }
}