import React,{Component} from 'react';

class NameForm extends Component {
    constructor(props) {
        super(props);
        this.state = {value: ''};

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }

    handleSubmit(event) {
        alert('A name was submitted: ' + this.state.value);
        event.preventDefault();
    }

    render() {
        return (
        <form className="rounded border border-dark">
            <div className="form-row mx-3">
                <div className="form-group col-md-3 mt-3">
                    <label htmlFor="exampleFormControlSelect2">Select Data Object</label>
                    <select multiple className="form-control" id="exampleFormControlSelect1">
                        <option>Density</option>
                        <option>Distance</option>
                        <option>Violations</option>
                        <option>Time</option>
                        <option>Frequency</option>
                    </select>
                </div>
                <div className="form-group col-md-3 mt-3">
                    <label htmlFor="exampleFormControlInput1"> Enter Expression</label>
                    <select className="form-control" id="exampleFormControlSelect2">
                        <option>Greater Than</option>
                        <option>Lesser Than</option>
                        <option>Equal To</option>
                        <option>AND</option>
                        <option>OR</option>
                    </select>
                </div>
            </div>
            <div className="form-row mx-3">
                <div className="form-group col-md-3">
                    <label htmlFor="inputZip">Input Value</label>
                    <input type="text" className="form-control" id="inputZip"/>
                </div>
                <div className="form-group col-md-3">
                    <label htmlFor="exampleFormControlSelect1">Select Action</label>
                    <select className="form-control" id="exampleFormControlSelect1">
                        <option>Alert</option>
                        <option>Sent Email</option>
                        <option>Alarm</option>
                        <option>Sent Text</option>
                    </select>
                </div>
            </div>
            <div className="form-group mx-3">
                <label htmlFor="exampleFormControlTextarea1">Description</label>
                <textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
            </div>
            <button className="btn btn-success mb-3 ml-3"> Submit </button>
        </form>
    );
    }
}

export default NameForm;
