import React, {Component} from "react";
import MUIDataTable from "mui-datatables";

/*export default class Rules extends Component{
    constructor(props) {
        super(props);
    };*/

function RuleList() {

    const columns = ["S No.","Rule Name", "Spaces", "Action","Description"];

    // Generate random data
    var names = [ 'Density Rule', 'Distance Rule', 'Frequency Rule', 'Amenity Usage Count', 'Net Violations'];
    var spaces = [ 'Meeting Room', 'Floor 2', 'Hallway 3', 'Office 1', 'Building', 'Dining Hall' ];
    var alerts = [ 'Sent Email', 'Alarm', 'Sent Text', 'Alert Staff'];
    var rule_desc = ['This rule manages the count to be lesser than 30',
                    'This rule will alert the staff to come clean after 12 uses',
                    'This rule gives and occupancy update on every entry exit',
                    'This rule keeps track of the violations per day'];

    var data = [];
    for (var i = 0; i < 1000; i++) {
        data.push({
            name: names[~~(Math.random() * names.length)],
            city: spaces[~~(Math.random() * spaces.length)],
            address: alerts[~~(Math.random() * alerts.length)]
        });

        data[i] = [i,names[~~(Math.random() * names.length)], spaces[~~(Math.random() * spaces.length)], alerts[~~(Math.random() * alerts.length)],rule_desc[~~(Math.random() * rule_desc.length)]];
    }


    /* var columns = [
         { title: 'Name', prop: 'name'  },
         { title: 'City', prop: 'city' },
         { title: 'Address', prop: 'address' }
     ];*/

    const options = {
        filterType: 'checkbox',
        rowsPerPageOptions: [5,10,15,50],
        print:false,
    };

    return(
        <MUIDataTable
            title={"Rule List"}
            data={data}
            columns={columns}
            options={options}
            >

        </MUIDataTable>
    );
}

export default RuleList;